import React from "react";
import styles from "./Guest.module.css";
import { Outlet } from "react-router";
import Header2 from "./header2";

const Main = () => {
  return (
    <div className={styles.container}>
      <Header2 />
      <main className={styles.main}>{<Outlet />}</main>
    </div>
  );
};

export default Main;
